import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { AuthService } from '../../core/services/auth.service';
import { Router } from '@angular/router';
import { ErrorService } from '../../core/services/error.service';


@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent {
  loginClicked = false;

  constructor(
    private authService: AuthService,
    private errorService: ErrorService,
    private router: Router) {
  }

  loginJwtToken(loginForm: NgForm): void {
    this.errorService.setErrorData(null);
    if (!loginForm.valid) {
      return;
    }
    if (!this.loginClicked) {
      this.loginClicked = true;
    }
    const username = loginForm.value.email;
    const password = loginForm.value.password;
    this.authService.loginJwtToken(username, password).subscribe(
      () => {
        this.router.navigateByUrl('/wygeneruj-rachunek');
      },
      () => {
        this.loginClicked = false;
      });
  }
}
